:root {
  --WHITE: #fff;
  --Gray-Blue: #6a7b85;
  --Gray: #353d44;
}

* {
  margin: 0;
  padding: 0;
  font-family: Poppins, sans-serif;
}

.search {
  background-image: linear-gradient(rgba(253, 252, 252, 0), rgba(255, 255, 255, 0)), url("mountain.b14afb96.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.is-hidden {
