  color: var(--WHITE);
  align-content: center;
  padding: 15px 0 15px 5px;
  font-size: 19px;
  font-weight: 200;
  line-height: 1.17;
  display: flex;
}

a {
  color: currentColor;
  text-decoration: none;
}

