  padding: 25px;
  display: flex;
}

.photo-card {
  cursor: pointer;
  flex-direction: column;
  align-items: center;
  gap: 8px;
  display: flex;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

.photo-img {
  width: 325px;
  height: 225px;
}

.info {
  flex-direction: row;
  justify-content: space-evenly;
  gap: 4px;
  padding: 0 8px 8px;
  display: flex;
}

.info-item {
  text-align: center;
  flex-direction: column;
  display: flex;
}

.btn-loab-more {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.load-more {
  letter-spacing: .02em;
  color: var(--WHITE);
  background-color: var(--Gray-Blue);
  cursor: pointer;
  border: none;
  width: 75px;
  padding: 4px;
  font-size: 11px;
  font-style: normal;
  font-weight: 200;
  line-height: 1.5;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

